@import "./vars.scss";
@import url('https://fonts.googleapis.com/css2?family=League+Spartan:wght@700&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'League Spartan', sans-serif;
  font-weight: 700;
}
.container{
  display: grid;
  min-height: 100vh;
  place-content: center;
  background-color: $first_back;
}
.top{
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
  & p{
    font-size: 25px;
    color: white;
  }
  & h6{
    color: white;
  }
}

.theme-btn{
  color: white;
  background-color: transparent;
  border: 0;
  cursor: pointer;
  margin-right: 8px;
}
.theme{
  display: flex;
  text-align: center;
  align-items: flex-end;  
}
.theme-buttons{
  background-color: $first_togle_back;
  border-radius: 13px;
  height: 26px;
  display: flex;
  justify-content: space-between;
  padding: 5px 0 5px 5px;
  margin: 0;
  :not(last-childe){
    margin-right: 5px;
  }
}
.theme-btn{
  height: 1rem;
  width: 1rem;
}
.toggle_active{
  width: 1rem;
  height: 1rem;
  border-radius: 0.5rem;
  background-color: $first_toggle_red;
}
.one{
  &::before{
    position: absolute;
    content: "1";
    width: 1rem ;
    height: 1rem;
    transform: translate(-50%,-150%);
  }
}
.two{
  &::before{
    position: absolute;
    content: "2";
    width: 1rem ;
    height: 1rem;
    transform: translate(-50%,-150%);
  }
}
.three{
  &::before{
    position: absolute;
    content: "3";
    width: 1rem ;
    height: 1rem;
    transform: translate(-50%,-150%);
  }
}
.theme-text{
  margin-right: 1rem;
  font-size: 15px;
  
}
.main_buttons{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  column-gap: 1rem;
  row-gap: 1rem;
}
.second_buttons{
  display: flex;
  justify-content: space-between;
}
.middle{
  height: 1rem;
  background-color: $first_res_bak;
  height: 127px;
  color: white;
  display: flex;
  justify-content: right ;
  align-items: center;
  border-radius: 8px;
  margin-top: 30px;
  margin-bottom: 24px;
  font-size: 2rem;
  padding: 1rem;
}
.botton{
  padding: 30px;
  background-color: $first_togle_back;
  border-radius: 8px;
}
.main_btn{
  height: 60px;
  width: 100px;
  background-color: $first_keybackground_orange;
  border-top: 0;
  border-right: 0;
  border-left: 0;
  border-radius: 9px;
  cursor: pointer;
  font-size: 24px;
  color: $first_keyshadow;
}
.main_btn_del{
  height: 60px;
  width: 100px;
  background-color: $first_keybackground;
  color: white;
  border-top: 0;
  border-right: 0;
  border-left: 0;
  border-radius: 9px;
  cursor: pointer;
  font-size: 24px;
}
.second_buttons{
  margin-top: 1rem;
}
.reset{
  width: 200px;
  height: 60px;
  background-color: $first_keybackground;
  color: white;
  font-size: 20px;
  border: 0;
  border-radius: 8px;
  cursor: pointer;
}
.sum{
  width: 200px;
  height: 60px;
  background-color: $first_toggle_red;
  color: white;
  font-size: 20px;
  border: 0;
  border-radius: 8px;
  cursor: pointer;
}
